.vaiFooter {
  background-color: transparent;
  padding: 10px 10px;
  width: 100vw;
  text-align: center;
  position: absolute;
  bottom: 0;
}

.vaiFooterText {
  font-weight: 400;
  font-size: 12px;
  line-height: 14.32px;
  color: #fafafa;
}

@media (max-width: 514px) {
  .vaiFooter {
    margin-bottom: 50px;
    bottom: 0;
  }
}

@media (max-width: 514px) and (max-height: 819px) {
  .vaiFooterTextGray {
    color: #6d6d73;
  }
}
