.body {
  position: relative;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  min-height: 700px;
}

.panel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: -10px;
  background-color: #fafafa;
  width: 520px;
  border-radius: 10px;
}

.skyLogo {
  height: 50px;
  width: auto;
  padding: 10px;
}

.title {
  color: #6d6d73;
  font-weight: 300;
  font-size: 28px;
  margin-top: 25px !important;
  line-height: 35.8px;
}

.description,
.errorAlertMessage {
  font-weight: 400;
  font-size: 14px;
  line-height: 17.9px;
  width: 390px !important;
  margin-left: 60px;
  margin-top: 30px !important;
  color: #6d6d73;
}

.errorAlertMessage {
  margin-top: 10px !important;
}

.emailInput {
  font-size: 14px;
  line-height: 16.71px;
  font-weight: 400;
  color: #6d6d73;
  background: #fafafa;
  width: 389px !important;
  height: 33px;
  padding: 8px 15px 8px 5px;
  margin-top: 30px;
  margin-left: 62px;
  box-shadow: inset 0px -1px 0px #6d6d73;
  border: none;
  outline: none;
}

.resetPasswordButton,
.redirectToSignInPageButton {
  font-size: 18px;
  font-weight: 100;
  line-height: 21px;
  color: #ffffff;
  background-color: #008662;
  width: 390px !important;
  height: 43px;
  padding: 11px 24px;
  margin-left: 70px;
  border-radius: 4px;
  border: none;
  margin-top: 40px !important;
}

.resetPasswordButton:disabled {
  opacity: 0.8;
  cursor: default;
}

.redirectUrlText {
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.03em;
  color: #6d6d73;
  margin-top: 15px !important;
}

/* Password Initiated */
.passwordInitiatedPanel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: -10px;
  background-color: #fafafa;
  height: 442.37px;
  width: 530px;
  border-radius: 10px;
}

@media (max-width: 515px) {
  .panel {
    padding-top: 50px;
  }
}
